import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  Output,
  ViewChild,
} from "@angular/core";
import Quagga, {
  QuaggaJSConfigObject,
  QuaggaJSResultObject,
} from "@ericblade/quagga2";
import { Plugins } from "@capacitor/core";
import { BarcodeScannerPlugin } from "@dutchconcepts/capacitor-barcode-scanner";
import { Platform } from "@ionic/angular";

const BarcodeScanner = Plugins.BarcodeScanner as BarcodeScannerPlugin;

export enum BARCODE_TYPE {
  code_128 = "code_128",
  code_39 = "code_39",
  code_39_vin = "code_39_vin",
  ean = "ean",
  ean_extended = "ean_extended",
  ean_8 = "ean_8",
  upc = "upc",
  upc_e = "upc_e",
  codabar = "codabar",
  i2of5 = "i2of5",
  "2of5" = "2of5",
  code_93 = "code_93",
}

type ScannerState = "off" | "on" | "starting" | "stopping";

@Component({
  selector: "app-scanner",
  templateUrl: "./scanner.component.html",
  styleUrls: ["./scanner.component.scss"],
})
export class ScannerComponent implements OnDestroy, AfterViewInit {
  @ViewChild("scanner")
  private barcodeScanner: ElementRef<HTMLDivElement>;

  private _scannerState: ScannerState = "off";
  public get scannerState(): ScannerState {
    return this._scannerState;
  }
  public readonly useWebScanner: boolean;

  @Input()
  public deviceId: string;

  @Input()
  public types: BARCODE_TYPE[];

  @Output()
  public readonly scanned = new EventEmitter<string>();

  @Output()
  public readonly canceled = new EventEmitter<void>();

  public constructor(platform: Platform) {
    this.useWebScanner = !platform.is("hybrid");
  }

  public async start(): Promise<void> {
    if (this.scannerState !== "off") {
      return;
    }

    this._scannerState = "starting";

    if (this.useWebScanner) {
      await this.initWebScanner();
      Quagga.start();
      Quagga.onProcessed(this.detectProcessed.bind(this));
    } else {
      await BarcodeScanner.hideBackground();

      const status = await BarcodeScanner.checkPermission({ force: true });
      if (!status.granted) {
        console.log("Permission was not granted.");
        this._scannerState = "off";

        this.canceled.emit();
        // TODO Hanle denied (android)
      }

      this._scannerState = "on";

      const result = await BarcodeScanner.startScan(); // start scanning and wait for a result
      await this.stop();
      // if the result has content
      if (result.hasContent) {
        this.scanned.emit(result.content); // log the raw scanned content
      } else {
        this.canceled.emit();
      }
    }
  }

  public async stop(): Promise<void> {
    if (this.scannerState === "off") {
      return;
    }
    this._scannerState = "stopping";
    if (this.useWebScanner) {
      this._scannerState = "stopping";
      Quagga.stop();
    } else {
      await BarcodeScanner.stopScan();
    }
    this._scannerState = "off";
  }

  public ngOnDestroy(): void {
    void this.stop();
  }

  public ngAfterViewInit(): void {
    void this.start();
  }

  private detectProcessed() {
    Quagga.offProcessed(this.detectProcessed.bind(this));
    this._scannerState = "on";
  }

  private async onDetected(result: QuaggaJSResultObject): Promise<void> {
    await this.stop();
    this.scanned.next(result.codeResult.code);
  }

  private initWebScanner(): Promise<void> {
    return new Promise((resolve, reject) => {
      Quagga.onDetected((result) => void this.onDetected(result));

      const config = {
        inputStream: {
          name: "Live",
          type: "LiveStream",
          target: this.barcodeScanner.nativeElement,
          constraints: {
            width: { min: 640 },
            height: { min: 480 },
            aspectRatio: { min: 1, max: 2 },
            facingMode: "environment", // or user
            deviceId: this.deviceId,
          },
          singleChannel: false, // true: only the red color-channel is read
        },
        locator: {
          patchSize: "medium",
          halfSample: true,
        },
        locate: true,
        numOfWorkers: 4,
        decoder: {
          readers: this.types.map((val) => `${val}_reader`),
        },
      } as QuaggaJSConfigObject;

      Quagga.init(config, (err) => {
        if (err) {
          console.log(err);
          return reject(err);
        }
        return resolve();
      });
    });
  }
}
