import { AfterViewInit, Component, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { ViewDidLeave, ViewWillEnter } from "@ionic/angular";
import { ProductsApiService } from "src/app/api";
import { ScannerComponent } from "src/app/components/scanner/scanner.component";

@Component({
  selector: "app-scan",
  templateUrl: "./scan.page.html",
  styleUrls: ["./scan.page.scss"],
})
export class ScanPage implements AfterViewInit, ViewWillEnter, ViewDidLeave {
  @ViewChild(ScannerComponent)
  private scanner: ScannerComponent;

  public constructor(
    private readonly router: Router,
    private readonly productsApiService: ProductsApiService,
  ) {}

  public async cancel(): Promise<void> {
    await this.router.navigate(["/"], { replaceUrl: true });
  }

  public async scanned(ean: string): Promise<void> {
    try {
      // TODO Dedicated call for only id
      const result = await this.productsApiService
        .productsGetProductByEan({ ean })
        .toPromise();
      await this.router.navigate(["product", result.id], { replaceUrl: true });
    } catch {
      await this.router.navigate(["scan-failed", ean], { replaceUrl: true });
    }

    await this.stop();
  }

  private async start(): Promise<void> {
    await this.scanner.start();
  }

  private stop(): Promise<void> {
    return this.scanner.stop();
  }

  public ionViewDidLeave(): void {
    void this.stop();
  }

  public ionViewWillEnter(): void {
    void this.start();
  }

  public ngAfterViewInit(): void {
    void this.start();
  }
}
